import React from 'react'

import styles from './App.module.sass'
import '../styles/global.sass'

import { useTimePeriod } from '../hooks/useTimePeriod'
import EventsSlider from './EventSlider/EventSlider'
import HistoricalDates from './HistoricalDates/HistoricalDates'
import { TimePeriod } from '../types/TimePeriod'
import TimePeriodController from './TimePeriodController/TimePeriodController'
import Pagination from './shared/Pagination/Pagination'

const TIME_PERIODS: TimePeriod[] = [
  {
    title: 'Наука',
    from: 1992,
    to: 1997,
    events: [
      {
        year: 1992,
        text: '13 сентября — частное солнечное затмение, видимое в Южной Африке и части Антарктиды',
      },
      {
        year: 1994,
        text: 'Телескоп «Хаббл» обнаружил самую удалённую из всех обнаруженных галактик, получившую обозначение GN-z11',
      },
      {
        year: 1995,
        text: 'Компания Tesla официально представила первый в мире электрический грузовик Tesla Semi',
      },
      { year: 1997, text: 'Гарри' },
    ],
  },
  {
    title: 'Кино',
    from: 1987,
    to: 1991,
    events: [
      { year: 123, text: 'Нобелевская премия' },
      { year: 1994, text: 'Бессоница' },
      { year: 1995, text: 'Лит' },
      { year: 1997, text: 'Гарри' },
    ],
  },
  {
    title: 'Литература',
    from: 1992,
    to: 1997,
    events: [
      { year: 321, text: 'Нобелевская премия' },
      { year: 1994, text: 'Бессоница' },
      { year: 1995, text: 'Лит' },
      { year: 1997, text: 'Гарри' },
    ],
  },
  {
    title: 'Литература',
    from: 1992,
    to: 1997,
    events: [
      { year: 321, text: 'Нобелевская премия' },
      { year: 1994, text: 'Бессоница' },
      { year: 1995, text: 'Лит' },
      { year: 1997, text: 'Гарри' },
    ],
  },
  {
    title: 'Литература',
    from: 1992,
    to: 1997,
    events: [
      { year: 321, text: 'Нобелевская премия' },
      { year: 1994, text: 'Бессоница' },
      { year: 1995, text: 'Лит' },
      { year: 1997, text: 'Гарри' },
    ],
  },
  {
    title: 'Литература',
    from: 1992,
    to: 1997,
    events: [
      { year: 321, text: 'Нобелевская премия' },
      { year: 1994, text: 'Бессоница' },
      { year: 1995, text: 'Лит' },
      { year: 1997, text: 'Гарри' },
    ],
  },
]

const App: React.FC = () => {
  const {
    currentPeriod,
    previousPeriod,
    activePeriodIndex,
    changeTimePeriod,
    prevTimePeriod,
    nextTimePeriod,
  } = useTimePeriod(TIME_PERIODS)

  const totalCount = TIME_PERIODS.length

  return (
    <div className={styles.wrapper}>
      <HistoricalDates
        currentPeriod={currentPeriod}
        previousPeriod={previousPeriod}
        activePeriodIndex={activePeriodIndex}
        totalCount={totalCount}
        onPeriodChange={changeTimePeriod}
        onPrev={prevTimePeriod}
        onNext={nextTimePeriod}
      />
      <div className={styles.container}>
        <EventsSlider events={currentPeriod.events} />
      </div>
      <div className={styles.timePeriodController}>
        <TimePeriodController
          currentIndex={activePeriodIndex + 1}
          onNext={nextTimePeriod}
          onPrev={prevTimePeriod}
          totalCount={totalCount}
        />
        <div className={styles.pagination}>
          <Pagination
            currentIndex={activePeriodIndex}
            totalCount={totalCount}
            onClick={changeTimePeriod}
          />
        </div>
      </div>
    </div>
  )
}

export default App
